<template>
  <div>
    <div class="registerContainer">
      <div class="title">会员认证</div>
      <div>
        <div class="item tel" :class="stateTel">
          <span></span>
          <div>
            <mt-field
              type="text"
              placeholder="请输入手机号"
              v-model="tel"
              :attr="{ maxLength: 11 }"
              @blur.native.capture="checkTel"
            ></mt-field>
          </div>
        </div>
        <div class="item name" :class="stateName">
          <span></span>
          <div>
            <mt-field
              type="text"
              placeholder="请输入姓名"
              v-model="name"
              @blur.native.capture="checkName"
            >
            </mt-field>
          </div>
        </div>
        <div class="item pwd" :class="statePassword">
          <span></span>
          <div>
            <mt-field
              type="password"
              placeholder="请输入密码"
              v-model="password"
              @blur.native.capture="checkPassword"
            >
            </mt-field>
          </div>
        </div>
        <div class="item conpwd" :class="stateConPassword">
          <span></span>
          <div>
            <mt-field
              type="password"
              placeholder="请输入确认密码"
              v-model="conPassword"
              @blur.native.capture="checkConPassword"
            >
            </mt-field>
          </div>
        </div>
        <div class="item cardtype" :class="stateCardType">
          <span></span>
          <div>
            <mt-field
              disabled
              type="text"
              placeholder="请选证件类型"
              v-model="cardType.name"
              @click.native.capture="showPopup(true)"
            >
              <img src="../assets/arrow_right.png" />
            </mt-field>
          </div>

          <mt-popup
            v-model="isshowPopup"
            :lockScroll="true"
            :closeOnClickModal="false"
          >
            <mt-radio
              :options="cardTypeList"
              v-model="selectedCardType"
            ></mt-radio>
            <div>
              <mt-button
                type="primary"
                size="small"
                @click.native.capture="selectCardType"
                >确认</mt-button
              >
              <mt-button
                type="primary"
                size="small"
                @click.native.capture="showPopup(false)"
                >取消</mt-button
              >
            </div>
          </mt-popup>
        </div>
        <div class="item cardnum" :class="stateCardnum">
          <span></span>
          <div>
            <mt-field
              type="text"
              placeholder="请输入证件号"
              v-model="cardnum"
              @blur.native.capture="checkCardnum"
            ></mt-field>
          </div>
        </div>

        <p>注：每个手机号码只能绑定一个证件号</p>
        <div class="item tyzm" :class="stateTyzm">
          <span></span>
          <div>
            <mt-field
              type="text"
              placeholder="请输入图形验证码"
              v-model="tyzm"
              @blur.native.capture="checkTyzm"
            >
              <div>
                <img src="../assets/imagecode.png" />
                <p>看不清换一个</p>
              </div>
            </mt-field>
          </div>
        </div>
        <div class="item messageyzm" :class="stateMessageyzm">
          <span></span>
          <div>
            <mt-field
              type="text"
              placeholder="请输入短信验证码"
              v-model="messageyzm"
              @blur.native.capture="checkMessageyzm"
              ><mt-badge size="small" type="primary"
                >获取验证码</mt-badge
              ></mt-field
            >
          </div>
        </div>
        <mt-button type="default" size="large" @click.native.capture="submit"
          >提交</mt-button
        >
      </div>
    </div>
    <my-footer></my-footer>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 手机号
      tel: "",
      stateTel: "",

      // 姓名
      name: "",
      stateName: "",

      // 密码
      password: "",
      statePassword: "",

      // 确认密码
      conPassword: "",
      stateConPassword: "",

      // 证件类型
      cardType: { name: "", id: "" },
      stateCardType: "",

      // 证件号码
      cardnum: "",
      stateCardnum: "",

      // 图片验证码
      stateTyzm: "",
      tyzm: "",

      // 短信验证码
      messageyzm: "",
      stateMessageyzm: "",

      // 是否显示证件类型选择框
      isshowPopup: false,

      // 选中的证件类型
      selectedCardType: "1",

      // 证件类型选项列表
      cardTypeList: [
        {
          label: "身份证222",
          value: "1",
        },
        {
          label: "户口薄",
          value: "2",
        },
        {
          label: "港澳台居民来往大陆通行证",
          value: "3",
        },
        {
          label: "军官证",
          value: "4",
        },
        {
          label: "护照",
          value: "5",
        },
      ],
    };
  },
  methods: {
    // ^(?![0-9]+$)(?![a-zA-Z]+$)(?![0-9a-zA-Z]+$)(?![0-9\\W]+$)(?![a-zA-Z\\W]+$)[0-9A-Za-z\\W]{6,18}$;
    // 其中(?![0-9a-zA-Z]+$)表示排除只有字母和数字的组合
    // (?![0-9\\W]+$)表示排除只有数字和特殊字符的组合
    // (?![a-zA-Z\\W]+$)表示排除只有字母和特殊字符的组合;
    // 手机验证
    checkTel() {
      var telRegExp = /^1[3578]\d{9}$/;
      if (!telRegExp.test(this.tel)) {
        this.stateTel = "error";
        return false;
      }
      this.stateTel = "";
      return true;
    },

    // 姓名验证
    checkName() {
      if (this.name == "") {
        this.stateName = "error";
        return false;
      }
      this.stateName = "";
      return true;
    },

    // 密码验证
    checkPassword() {
      var passwordRegExp = /^[a-zA-Z0-9_]{6,20}$/;
      if (!passwordRegExp.test(this.password)) {
        this.statePassword = "error";
        return false;
      }
      this.statePassword = "";
      return true;
    },

    // 确认密码验证
    checkConPassword() {
      if (this.password != this.conPassword) {
        this.stateConPassword = "error";
        return false;
      }
      this.stateConPassword = "";
      return true;
    },

    // 证件类型验证
    checkCardType() {
      if (this.cardType.name == "") {
        this.stateCardType = "error";
        return false;
      }
      this.stateCardType = "";
      return true;
    },

    // 证件号码验证
    checkCardnum() {
      if (this.cardnum == "") {
        this.stateCardnum = "error";
        return false;
      } else {
        var re = false;
        switch (this.cardType.id) {
          case "1": //身份证
            re = /^[0-9]{17}[0-9Xx]$|^[0-9]{15}$/.test(this.cardnum);
            break;
          case "3": //港澳台居民来往大陆通行证
            re =
              /^[HMhm]{1}([0-9]{10}|[0-9]{8})$/.test(this.cardnum) ||
              /^[0-9]{8}$/.test(this.cardnum) ||
              /^[0-9]{10}$/.test(this.cardnum);
            break;
          case "4": //军官证
            re = /^[0-9]{8}$/.test(this.cardnum);
            break;
          case "5": //护照
            re =
              /^[a-zA-Z]{5,17}$/.test(this.cardnum) ||
              /^[a-zA-Z0-9]{5,17}$/.test(this.cardnum);
            break;
        }
        if (re) {
          this.stateCardnum = "";
          return true;
        } else {
          return false;
        }
      }
    },

    // 图片验证码验证
    checkTyzm() {
      if (this.tyzm == "") {
        this.stateTyzm = "error";
        return false;
      }
      this.stateTyzm = "";
      return true;
    },

    // 短信验证码验证
    checkMessageyzm() {
      if (this.messageyzm == "") {
        this.stateMessageyzm = "error";
        return false;
      }
      this.stateMessageyzm = "";
      return true;
    },
    //点击证件类型确认按钮
    selectCardType() {
      // for (var i = 0; i < this.cardTypeList.length; i++) {
      //   if (this.cardTypeList[i].value == this.selectedCardType) {
      //     this.cardType.name = this.cardTypeList[i].label;
      //     this.cardType.id = this.cardTypeList[i].value;
      //     this.isshowPopup = false;
      //   }
      // }

      // 通过抛出异常的方式跳出forEach
      try {
        this.cardTypeList.forEach((item) => {
          if (item.value == this.selectedCardType) {
            this.cardType.name = item.label;
            this.cardType.id = item.value;
            throw Error();
          }
        });
      } catch (error) {
        this.isshowPopup = false;
        this.stateCardType = "";
      }
    },

    // 提交表单验证
    submit() {
      if (
        this.checkTel() &&
        this.checkName() &&
        this.checkPassword() &&
        this.checkConPassword() &&
        this.checkCardType() &&
        this.checkCardnum() &&
        this.checkTyzm() &&
        this.checkMessageyzm()
      ) {
        localStorage.setItem("username", this.name);
        localStorage.setItem("tel", this.tel);
        localStorage.setItem("islogined", true);
        this.$router.push("/mindex");
        // // ajax注册开始
        // var data = {
        //   tel: this.tel,
        //   password: this.password,
        //   name: this.name,
        //   cardType: this.cardType,
        //   cardnum: this.cardnum,
        // };
        // this.axios.post("/register", this.qs.stringify(data)).then((res) => {
        //   if (res.data.code == 201) {
        //     this.$messagebox.alert("注册失败", "注册提示");
        //   }
        //   if (res.data.code == 200) {
        //     this.$router.push("/");
        //   }
        // });
        // // ajax注册结束;
      }
    },
    // 弹出证件类型选择框
    showPopup(show) {
      this.isshowPopup = show;
    },
  },
};
</script>
<style>
.registerContainer {
  background-color: #2281d5;
}
.registerContainer .title {
  background-color: #f2f2f2;
}
.registerContainer > div:nth-child(2) {
  padding: 0 1rem;
  color: #fcfcfc;
  margin-top: 1.5rem;
  padding-bottom: 1rem;
}

.registerContainer button {
  color: #2281d5;
}

.registerContainer .mint-popup {
  border-radius: 1rem;
  padding: 1rem 1rem;
  width: 70%;
}
.registerContainer .mint-popup > div:nth-child(2) {
  text-align: center;
}
.registerContainer .mint-popup > div:nth-child(2) button {
  color: #fcfcfc;
  padding-left: 0 1rem;
}
.registerContainer .mint-popup > div:nth-child(2) button:nth-child(2) {
  margin-left: 2rem;
  color: #b6b6b6;
  background-color: #fcfcfc;
  border: 0.001rem solid #b6b6b6;
}

.registerContainer .mint-radiolist .mint-cell,
.registerContainer .mint-radiolist .mint-field-core {
  background-color: transparent;
  color: black;
}

.registerContainer p {
  text-align: center;
}

.registerContainer .item {
  display: flex;
  margin: 0.5rem auto;
}

.registerContainer .item > span {
  display: block;
  background: url(../assets/register_bg.png) no-repeat -7% center #69a9e2;
  width: 3rem;
  background-size: 500%;
}
.registerContainer .cardtype > span,
.registerContainer .cardnum > span {
  background-position: 21% 56%;
}
.registerContainer .cardtype img {
  width: 66%;
}

.registerContainer .tel > span {
  background-position: 49% 49%;
}
.registerContainer .tyzm > span {
  background-position: 78% 60%;
}
.registerContainer .messageyzm > span {
  background-position: 107% 49%;
}
.registerContainer .messageyzm {
  margin-bottom: 2rem;
}

.registerContainer .item > div {
  flex: 1;
}

.registerContainer .tyzm p {
  font-size: 0.2rem;
  margin-top: 0.2rem;
}
.registerContainer .tyzm img {
  width: 4.3rem;
  height: 1rem;
}

